/* stylelint-disable font-family-name-quotes */
@font-face {
  font-family: 'Avenir';
  src: url('./fonts/Avenir-Book.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Avenir';
  src: url('./fonts/Avenir-Heavy.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
}

body {
  margin: 0;
  font-family: 'Avenir', Arial, sans-serif;
  font-weight: 300;
  padding: 20px 0;

  --main-color: #e1e7ed;
  --secondary-color: #3d4e61;
}

.search {
  margin: 0 8px;
}

.search__input--big {
  margin-bottom: 20px;
  width: 100%;
  height: 70px;
  color: var(--secondary-color);
  font-family: 'Avenir', Arial, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border-radius: 4px;
  border: 1px solid var(--main-color);
  box-sizing: border-box;
  background-image: url(./images/Search.svg);
  background-repeat: no-repeat;
  background-size: 19px;
  background-position: center;
  background-position-x: 25.5px;
  padding-left: 62px;
  box-shadow: 0 1px 8px rgba(61, 67, 97, 0.1);
}

.search__input--big:hover {
  box-shadow: 0 3px 8px rgba(61, 67, 97, 0.2);
}

.search__input--big:focus {
  box-shadow: 0 3px 8px rgba(61, 67, 97, 0.2);
  color: var(--secondary-color);
  font-weight: bold;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.search__input--big::placeholder {
  color: var(--secondary-color);
}

.search__input--small {
  width: 100%;
  height: 42px;
  color: var(--secondary-color);
  font-family: 'Avenir', Arial, sans-serif;
  font-weight: 300;
  border-radius: 4px;
  box-sizing: border-box;
  border: 1px solid var(--main-color);
  background-image: url(./images/Search.svg);
  background-repeat: no-repeat;
  background-size: 11px;
  background-position: center;
  background-position-x: 13px;
  padding-left: 33px;
  box-shadow: 0 1px 8px rgba(61, 67, 97, 0.1);
  text-shadow: 0 11px 8px rgba(255, 255, 255, 0.25);
}

.search__input--small::placeholder {
  color: var(--secondary-color);
  font-size: 15px;
}

.search__input--small:hover {
  box-shadow: 0 3px 8px rgba(61, 67, 97, 0.2);
  background-position-x: 11px;
}

.search__input--small:focus {
  box-shadow: 0 3px 8px rgba(61, 67, 97, 0.2);
  text-shadow: 0 0 0 rgba(255, 255, 255, 0.25);
  font-weight: 700;
  background-position-x: 11px;
}

@media (max-width: 1024px) {
  .search__input--small::placeholder {
    font-size: 14px;
  }
}

